body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
header.Header {
  background-color: #24292e;
  color: hsla(0,0%,100%,.75);
  padding-bottom: 12px;
  padding-top: 12px;
  z-index: 32;
}
header span.title {
  font-size: 18px;
}
h4.subtitle span{
  border-bottom: 1px solid gray;
}
.repo {
  border: 1px solid #e1e4e8;
  margin: 15px 0;
  padding: 10px 15px;
  border-radius: 3px;
  height: 130px;
}
.reposlist {
  overflow: auto;
  height: 700px;
}
.repo h3{
  margin-top: 10px;
  margin-bottom: 15px;
  font-size: 20px;
}
.repo h3 a, .repo p a{
  color: #0366d6;
}
.repo p a{
  font-size: 12px;
}
.repo .owner{
  border: 1px solid #8dbdf8;
  background-color: #cedff3;
  padding: 2px;
  border-radius: 3px;
}
.repo .repo-language-color{
  border-radius: 50%;
  display: inline-block;
  height: 12px;
  position: relative;
  top: 1px;
  width: 12px;
}

.repo-language-javascript {
  background-color: #f1e05a;
}
.repo-language-csharp {
  background-color: #178600;
}
.repo-language-shell {
  background-color: #89e051;
}
.repo-language-python {
  background-color: #3572A5;
}
.repo-language-go {
  background-color: #375eab;
}
.repo-language-html {
  background-color: #e34c26;
}
.repo-language-default {
  background-color: #f1e05a;
}

.repo .mr-3{
  margin-right: 16px!important;
}
.repo .info {
  font-size: 13px;
  padding: auto;
}
.commitslist{
  margin-top: 26px;
  overflow: auto;
  height: 850px;
}
.commit {
  border: 1px solid #eaecef;
  font-size: 12px;
  padding: 8px 10px;
  border-bottom: none !important;
}
.commit-div {
  padding: 0 !important;
}
.commit-title a {
  font-size: 14px;
  color: gray;
}
.commit span{
  color: gray;
}
.commit:hover {
  background-color: #cedff3;
  border: 1px solid #cedff3;
}
.commit .repo-title{
  font-size: 15px;
  color: gray;
}
.commit-avatar {
  border-radius: 3px;
  margin-right: 10px;
}

.usercard img{
  border-radius: 5px;
}
.text-gray, .text-gray a{
  color: #586069!important;
}


